//main styles for home/index page (shared with art and design/work pages)

//Draggable (Before & After)
.et-in-viewport-check {
  opacity: 0;
}

#before-after {
  position: relative;
  width: 100%;
  height: 100vh;
  min-height: 650px;
  z-index: 1;
  
  @include breakpoint(mobile-down) {
    max-height: 800px;
  }
}

#before-after img {
  max-width: 320px;
}

.view {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.view-before {
  z-index: 100;
  background: $near-white;
}

.view-after {
  z-index: 200;
  background: $blue;
}

#dragme {
  position: absolute;
  width: 2px;
  height: 100%;
  top: 0px;
  left: 0px;
  background-color: $blue;
  cursor: pointer;
  z-index: 300;
  transform: translate3d(0px, 0px, 0px);
  cursor: move;
  user-select: none;
  touch-action: none;
}

#dragme .icon-drag {
  width: 30px;
  height: 30px;
  background: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/138941/drag-icon.svg') no-repeat center center;
  display: block;
  position: absolute;
  top: calc(50% + 1.6rem);
  left: -15px;
  // margin-top: 1rem;
}

.wrapper-after,
.wrapper-before {
  position: relative;
  height: 100%;
}

.wrapper-after .img-sa-wrapper,
.wrapper-before .img-sa-wrapper {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  margin-top: -120px;
  z-index: 9;
}

.img-sa-wrapper {
  width: 330px;
  height: 330px;
  display: block;
}

.img-sa-wrapper img {
  width: 100%;
  height: auto;
}

.tooltip-item {
  width: 20px;
  height: 20px;
  margin: -15px 0 0 -15px;
  position: absolute;
  border-radius: 50%;
  border: 2px solid;
  animation: pulse-item 0.6s infinite alternate;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

@keyframes pulse-item {
  from {
    transform: scale3d(0.5, 0.5, 1);
  }
  to {
    transform: scale3d(1, 1, 1);
  }
}

.wrapper-after .tooltip-item {
  border-color: $white;
}

.wrapper-after .tooltip-item:hover {
  background: $white;
}

.wrapper-before .tooltip-item {
  border-color: $blue;
}

.wrapper-before .tooltip-item:hover {
  background: $blue;
}

//de-DRY
.wrapper-after .tooltip-item.tooltip-item-1,
.wrapper-before .tooltip-item.tooltip-item-1 {
  left: 81px;
  top: 6px;
}

.wrapper-after .tooltip-item.tooltip-item-2,
.wrapper-before .tooltip-item.tooltip-item-2 {
  left: 135px;
  top: 101px;
}

.wrapper-after .tooltip-item.tooltip-item-3,
.wrapper-before .tooltip-item.tooltip-item-3 {
  right: 155px;
  bottom: 35px;
}

.wrapper-after .tooltip-item.tooltip-item-4,
.wrapper-before .tooltip-item.tooltip-item-4 {
  right: 60px;
  bottom: 3px;
}

.content-before-after {
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: calc(50% - 100px);
  display: flex;
  align-content: center;
  justify-content: space-between;
  
  @include breakpoint(mobile-down) {
    display: none;
  }
}

.content-before-after .content-right {
  width: 50%;
  text-align: center;
}

.content-before-after .content-right h3 {
  color: $near-white;
  font-size: 24px;
  margin: 0 0 1em;
  // text-align: center;
}


//TODO: swap 'left/right'
.content-before-after.left-position .content-right h3 {
  color: $blue;
}

.content-before-after .content-left {
  width: 50%;
  margin: auto;
  text-align: center;
}

.content-left ul {
  list-style-type: none;
  padding-left: 0;
}

.content-before-after.right-position .content-detail-wrapper h3 {
  color: $white;
}

.content-before-after.right-position .content-detail-wrapper h3 span.color-main {
  color: $blue;
}

.content-before-after .content-detail-wrapper .img-position {
  width: 80px;
  height: auto;
  display: block;
  margin: 0 auto;
}

.content-before-after.right-position .content-detail-wrapper .img-position {
  border-color: $white;
}

.content-before-after .content-detail-wrapper .img-position img,
.content-before-after .content-detail-wrapper .img-position svg {
  width: 100%;
  max-width: 100%;
  height: auto;
}

.content-before-after .content-left ul li {
  display: none;
  position: absolute;
  top: 0;
  right: 10%;
}

.content-before-after .content-left ul li.active {
  display: block;
}

///////////////////////
////svg fill colors B&A
.wrapper-before {
  .fs-fill { fill: $fs-orange; }
  .ova-dark-fill { fill: $ova-db; }
  .ova-light-fill { fill: $ova-lb; }
  .vive-dark { fill: $charcoal; }
  .vive-blue { fill: $vive-blue; }
}

//after is blue bkgd
.wrapper-after {
  .fs-fill { fill: $white; }
  .ova-dark-fill { fill: $charcoal; }
  .ova-light-fill { fill: $dark-gray; }
  .vive-dark { fill: $charcoal; }
  .vive-blue { fill: $charcoal; }
}


//google image grid (home specific styles)
.home {
  .og-grid {
    li {
      &:nth-of-type(n+7) {
        @include breakpoint(large-down) {
          display: none;
        }
      }  
    }
  }
}


/////skills section
.skill-container {
  .skills {
    max-width: 75rem;
    margin: 3em auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .column {
      flex: 0 0 50%;
      min-height: 220px;
      min-width: 300px;
      margin: 0 auto;
      
      @media (min-width: 50rem) {
        flex: 0 0 33%;
        margin: auto;
      }
      
      &:nth-of-type(even) {
        @media (max-width: 50rem) {
          background-color: #f2f2f2;
        }
      }
    }


    .skill {
      position: relative;
      display: flex;

      &--content {
        position: relative;
        display: flex;
        flex: 0 0 100%;
        padding: 1em;
        margin: 0 auto;
        justify-content: space-between;
      }
      
      &--icon {
        flex: 0 0 2.6rem;

        svg {
          max-width: 2.6rem;
          max-height: 2.6rem;
          margin-top: 1em;

          & * {
            fill: #333;
            opacity: 1;
          }
        }
      }
      
      &--info {
        flex: 0 0 calc(100% - 4rem);
        text-align: left;

        h4, h5 {
          margin-bottom: 1.8em;
          text-align: left;
        }

        p {
          line-height: 1.6;
          margin-bottom: 1em;
          text-align: left;
        }
        
        ul.list-style-none {
          list-style-type: none;
          padding-left: 0;
          line-height: 1.6;
          text-align: left;
          
          li {
            margin-bottom: 1em;
            font-size: 1.2em;
            font-weight: 300;
          }
        }
      }
    }
  }
}



//////intro word sliding - animated heading

.cd-words-wrapper {
  display: inline-block;
  position: relative;
  text-align: left;
}
.cd-words-wrapper b {
  display: inline-block;
  position: absolute;
  white-space: nowrap;
  left: 0;
  top: 0;
}
.cd-words-wrapper b.is-visible {
  position: relative;
}


/* slide */
.cd-headline.slide span {
/*   padding: 0; */
  display: inline;
  text-align: left;
}
.cd-headline.slide .cd-words-wrapper {
  overflow: hidden;
  vertical-align: top;
  width: auto !important;
  display: inline-block;
  min-width: 6em !important;
}
.cd-headline.slide b {
  opacity: 0;
  top: .2em;
}
.cd-headline.slide b.is-visible {
  top: 0;
  opacity: 1;
  animation: slide-in 0.75s;
}
.cd-headline.slide b.is-hidden {
  animation: slide-out 0.75s;
}

@keyframes slide-in {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  60% {
    opacity: 1;
    transform: translateY(-10%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slide-out {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  60% {
    opacity: 0;
    transform: translateY(-90%);
  }
  100% {
    opacity: 0;
    transform: translateY(-100%);
  }
}